/*
/*/

.(sp/tab/pc)-only.(sp/tab/pc)

.(sp/tab/pc)-hidden.(sp/tab/pc)

.sp-onlytab/pc.pc-onlytab/sp

Markup:

Styleguide #{$guideNum}
============================================================*/
@media screen and (max-width: $breakpoint-sp) {
	.sp-hidden,
	.pc-only,
	.tab-only {
		display: none !important;
	}
}

@media screen and (min-width: $breakpoint-sp + 1) {
	.pc-hidden,
	.tab-only,
	.sp-only {
		display: none !important;
	}
}
